import { graphql, Link, useStaticQuery } from "gatsby"
import PropTypes from "prop-types"
import React from "react"
import styled from "styled-components"
import Img from "gatsby-image"

const THeader = styled.header`
  width: 100%;
  padding: 5px 10px 0 10px;
  position: fixed;
  top: 0;
  background-color: #e4e4e4;
  z-index: 2;
  border-bottom: 1px solid #5c5c5c;
  height: 60px;
`

const LinkRow = styled.div`
  float: right;
  margin-right: 10px;
  height: 30px;
`

const MainLink = styled.h1`
  display: inline-block;
  margin: 5px auto;
  font-size: 30px;
  a {
    color: #5c5c5c;
  }

`

export const BrandIcon = styled(Img)`
  width: 30px;
  display: inline-block;
  vertical-align: middle;
`

const HeaderUl = styled.ul`
  margin: 5px 0;
`

const HeaderLi = styled.li`
   padding: 0 18px;
   display: inline-block;
   position: relative;
`

const HeaderLink = styled(Link)`
    font-size: 16px;
    font-weight: 400;
    text-transform: uppercase;
    line-height: 42px;
    letter-spacing: .7px;
    color: #5c5c5c;
`

const HeaderLiNx = styled(HeaderLi)`
   &:after {
    background-color: #333;
    border-radius: 100%;
    width: 4px;
    height: 4px;
    bottom: 18.8px;
    right: -3px;
    position: absolute;
    content: "";
  }
`

const HeaderTitle = styled.span`
  vertical-align: middle;
  font-size: 35px;
  line-height: 35px;
`


const Header = ({ siteTitle }) => {
  const data = useStaticQuery(
    graphql`
      query {
        titleIcon: file(relativePath: { eq: "siteicon.png" }) {
          childImageSharp {
            fluid(maxWidth: 30) {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
    `
  )
  return (
    <THeader>
      <MainLink>
        <Link to="/" >
          <BrandIcon fluid={data.titleIcon.childImageSharp.fluid}   />
          <HeaderTitle>{siteTitle}</HeaderTitle>
        </Link>
      </MainLink>
      <LinkRow>
        <HeaderUl>
          <HeaderLiNx><HeaderLink to="/" >Home</HeaderLink></HeaderLiNx>
          <HeaderLiNx><HeaderLink to="/blog" >Blog</HeaderLink></HeaderLiNx>
          <HeaderLi><HeaderLink to="/aboutme" >About Me</HeaderLink></HeaderLi>
        </HeaderUl>
      </LinkRow>
    </THeader>
  )
}

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}


export default Header
