import React from "react"
import PropTypes from "prop-types"
import { StaticQuery, graphql, Link } from "gatsby"
import Header from "../components/header"
import "./layout.css"
import styled from "styled-components"
import Footer from "../components/footer"
import posed, { PoseGroup } from "react-pose"
import searchIcon from "../images/blog/search_icon.svg"
import Search from "../components/Search"

const searchIndices = [
  { name: `Posts`, title: `Blog Posts`, hitComp: `PostHit` },
]


const timeout = 300
const delay = 350

const Main = styled.div`
  min-width: 800px;
  width: 50%;
  text-align: center;
  margin: 60px auto 0;
`

const Wrapper = styled.div`
  padding: 15px 0;
`

const MainBody = styled.div`
padding: 15px 0;
`
const PagingFooter = styled.div`
  width: 100%;
  z-index: 5;
  bottom: 0;
  left: 0;
  position: fixed;
  text-align: center;
`

const Paging = styled.ul`
  background-color: #3c3c3c;
  margin: 0 auto;
  li {
    display: inline-block;
    padding: 5px 10px;
  }
`

const ActiveLink = styled(Link)`
  color: #DD8825;
  font-weight: bold;
  font-size: large;
`
const NonActiveLink= styled(Link)`
  color: #e4e4e4;
`


const SearchBar = styled.div`
  position: fixed;
  z-index: 3;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  margin: 20px auto;
`

const SearchInput = styled.input`
  border: none;
  border-radius: 100px;
  width: 500px;
  height: 20px;
  padding: 0 20px;
  background-image: url(${searchIcon});
  background-position: center right 10px;
  background-repeat: no-repeat;
`

const RoutesContainer = posed(Wrapper)({
  enter: {
    delay: delay,
    opacity: 1,
    delayChildren: timeout,
    transition: { ease: 'easeOut', duration: timeout}
  },
  exit: {
    opacity: 0,
    delayChildren: timeout,
    transition: { ease: 'easeIn', duration: timeout }
  },
})


const Layout = ({ children, location, pageContext }) => (
  <StaticQuery
    query={graphql`query SiteTitleQuery {
        site {
          siteMetadata {
            title
          }
        }
      }`}
    render={data => (
      <MainBody>
        <Header siteTitle={data.site.siteMetadata.title} />
        {(pageContext.layout === "blog" || pageContext.layout==="post") && (
          <SearchBar>
            <SearchInput type="text" id="name" name="name" placeholder="Search this blog" />
            <Search collapse indices={searchIndices} />
          </SearchBar>
        )}
        <Main>
          <PoseGroup>
            <RoutesContainer key={location.pathname}>{children}</RoutesContainer>
          </PoseGroup>
        </Main>

        {pageContext.layout !== "blog" && (
          <Footer siteOwner={"Hourai Etealune"} />
        )}

        {pageContext.layout === "blog" && (
          <PagingFooter>
            <Paging>
              {pageContext.currentPage!==1 && (
                <li key={'prevpage'}>
                  <NonActiveLink to={pageContext.currentPage - 1 === 1 ? "/blog/" : "/blog/" + (pageContext.currentPage - 1).toString()} rel="prev">
                    &lt;
                  </NonActiveLink>
                </li>
              )}
              {Array.from({ length: pageContext.numPages }, (_, i) => (
                <li key={'page'+i}>
                  { pageContext.currentPage===i+1 && (
                    <ActiveLink to={`/blog/${i === 0 ? "" : i + 1}`}>
                      {i + 1}
                    </ActiveLink>
                  )}
                  { pageContext.currentPage!==i+1 && (
                    <NonActiveLink to={`/blog/${i === 0 ? "" : i + 1}`}>
                      {i + 1}
                    </NonActiveLink>
                  )}
                </li>
              ))}
              {pageContext.currentPage!==pageContext.numPages && (
                <li key={'nextpage'}>
                  <NonActiveLink to={"/blog/" + (pageContext.currentPage + 1).toString()} rel="next">
                    &gt;
                  </NonActiveLink>
                </li>
              )}
            </Paging>
          </PagingFooter>
        )}

      </MainBody>
    )}
  />
)
Layout.propTypes = {
    children: PropTypes.node.isRequired,
}
export default Layout